<template>
    <div class="box">
        <div class="contain">
            <h1>这里放些平常遇到的问题</h1>
            <div v-for="(item,index) in exp" :key="index">
                <h2 class="que">{{ item.title }}</h2>
                <p class="answer" 
                v-for="(content,index) in item.contents" 
                :key="index">
                    {{ content }}
                </p>
                <img v-if="item.img" :src="item.img">
            </div>
        </div>
        <div class="videolist">
            <h3>网课推荐</h3>
            <ul>
                <li v-for="(video,index) in videos"
                :key="index"
                >
                    <a class="img" :href="video.href" target="_blank">
                        <img :src="video.img">
                    </a>
                    <a class="intro" :href="video.href" target="_blank">
                        {{ video.intro }}
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import { useStore } from 'vuex';

export default {
    name:'myHome',
    setup(){
        const store=useStore()
        let videos=store.state.videolist.videolist
        let exp=store.state.exp.exp
        let bmaincolor=store.state.bmaincolor
        return{videos,exp,bmaincolor}
    }
}
</script>
<style lang="less" scoped>
@bmaincolor:v-bind(bmaincolor);
    .box{
        display: flex;
        align-items: flex-start;
        .contain{
            flex: 1;
            margin: 0;
            padding-left: 10px;
            h1{
                margin-bottom: 20px;
                text-align: left;
                font-size: 25px;
            }
            div{
                margin-bottom: 30px;
                .que{
                    margin-bottom: 10px;
                    text-align: left;
                    color: rgb(95, 100, 107);
                }
                .answer{
                    text-align: left;
                    text-indent: 34px;
                }
                img{
                    width: 100%;
                }
            }
        }
        .videolist{
            width: 250px;
            margin-left: 10px;
            margin-right: 150px;
            padding-left: 5px;
            border-left: 2px solid;
            border-image: -webkit-linear-gradient(transparent , @bmaincolor , transparent)1 10 1; /* 控制边框颜色渐变 */
            border-image: -moz-linear-gradient(transparent , @bmaincolor , transparent)1 10 1;
            border-image: linear-gradient(transparent , @bmaincolor , transparent)1 10 1; /* 标准的必须写在最后 */
            h3{
                text-align: left;
            }
            li{
                width: 90%;
                margin: auto;
                margin-bottom: 10px;
                overflow: hidden;
                border-radius: 10px;
                box-shadow: 0 1px 3px 0 rgb(184, 188, 195);
                .img{
                    position: relative;
                    display: block;
                    width: 95%;
                    margin: auto;
                    margin-top: 2.5%;
                    overflow: hidden;
                    border-radius: 8px;
                    img{
                        width: 100%;
                    }
                    &::after{
                        content: '';
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        width: 100%;
                        height: 100%;
                        background-color: transparent;
                        transition: all .3s;
                    }
                    &:hover{
                        &::after{
                            background-color: rgba(0,0,0,.2);
                            backdrop-filter: blur(.5px);
                        }
                    }
                }
                .intro{
                    display: block;
                    width: 95%;
                    height: 30px;
                    margin: auto;
                    overflow: hidden;
                    white-space: nowrap; 
                    text-overflow: ellipsis;
                    text-align: left;
                    padding-left: 5px;
                    font-size: 15px;
                    line-height: 30px;
                    transition: all .3s;
                    &:hover{
                        color:@bmaincolor
                    }
                }
            }
        }
    }
</style>